* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  background: #000000;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
  min-width: 100vw;
  overflow-x: hidden;
}

